import React, { useContext, useEffect, useRef } from "react";

import { Grid } from "../Grid/Grid";
import { EditForm } from "../Form/EditForm";
import { Context } from "../../context/DatesContext";
import { Context as FormContext } from "../../context/FormContext";
import { Context as RentedContext } from "../../context/RentedFormContext";
import { Context as SocketContext } from "../../context/SocketContext";

import axios from "axios";

// import "./FormContainerStyle.css"; // Adjust the path as necessary
import { NewFormm } from "../Form/NewFormm";
export const Container = () => {
  const containerRef = useRef();
  const {
    loadIntervals_db_msg,
    addIntervalToArrayRentedMsg,
    deleteMsg,
    modifyIntervalToArrayRentedMsg,
  } = useContext(Context);
  const { userId, intervalRented } = useContext(FormContext).state;
  const { rented } = useContext(RentedContext).state;
  const { showEditForm } = rented;
  const { showForm } = intervalRented;
  const { socket } = useContext(SocketContext).state;

  const getRentedArray = async () => {
    try {
      const res = await axios.get(`/reservations/all`);
      loadIntervals_db_msg(res.data.userReservations);
    } catch (error) {
      console.error("Error fetching reservations:", error);
    }
  };

  useEffect(() => {
    socket.on("connect", () => {
      console.log("Connected to server");
      socket.on("login", (socket) => {
        console.log(`socket: ${socket}`);
      });
    });

    socket.on("disconnect", () => {
      console.log("Disconnected from server");
    });

    getRentedArray(); // Fetch reservations when component mounts

    return () => {
      socket.disconnect();
    };
  }, []);

  socket.emit("login", userId);

  useEffect(() => {
    socket.on("reservationCreated", (msg) => {
      console.log(msg);
      if (msg.user === userId && !msg.isOverLappingg) {
        addIntervalToArrayRentedMsg({
          ...msg,
          start: new Date(msg.start),
          end: new Date(msg.end),
        });
      }
    });
  }, [userId]);
  useEffect(() => {
    socket.on("updateReservation", (msg) => {
      console.log(msg);
      if (msg.user === userId && !msg.isOverLappingg) {
        modifyIntervalToArrayRentedMsg({
          ...msg,
          start: new Date(msg.start),
          end: new Date(msg.end),
          update: true,
        });
      }
    });
    socket.on("deleteReservation", (msg) => {
      deleteMsg(msg);
    });

    return () => {
      socket.off("updateReservation");
      socket.off("deleteReservation");
    };
  }, [socket]);

  return (
    <div>
      <div ref={containerRef}>
        <Grid />
        <h1 className="">Hello world!</h1>
        {showEditForm ? (
          <EditForm />
        ) : showForm ? (
          <NewFormm
            reservation={intervalRented.formType ? intervalRented : null}
          />
        ) : null}
      </div>
    </div>
  );
};
