import React, { useState, useEffect, useContext, useId, useRef } from "react";
import { Context } from "../../context/IdContext";
import "./Grid.css";
import { useDrag } from "react-dnd";
import { Context as FormContext } from "../../context/FormContext";
import { Context as RentedContext } from "../../context/RentedFormContext";
import { height } from "@mui/system";
export const Rented = ({
  w,
  className,
  rented,
  index,
  containerWidth,
  containerLeft,
  containerWidth2,
  containerLeft2,
  dragRef,
  containerRef,
  onmouseDownHandler,
  inView,
  cellH,
  ...props
}) => {
  const {
    state: { id },
    idMsg,
  } = useContext(Context);

  const [width, setWidth] = useState(0);
  const [left, setLeft] = useState(0);
  const rentedRef = useRef(null);

  const { state, formSubmittedMsg, showFormMsg } = useContext(FormContext);
  const { rentedMsg } = useContext(RentedContext);
  useEffect(() => {
    if (rentedRef.current !== null) {
      const RR = rentedRef.current.getBoundingClientRect();
      const left = RR.left - containerLeft;
      setLeft(left);

      setWidth(containerWidth - left);
    }
  });

  const [{ hover, isDragging }, drag] = useDrag(() => ({
    type: "RENTED",
    item: {
      rented,
    },

    // begin: (item, monitor) => console.log('BEGIN'),
    end: (item, monitor) => formSubmittedMsg(true),
    collect: (monitor) => {
      return {
        isDragging: !!monitor.isDragging(),
      };
    },
  }));

  const DclickHandle = (e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log(rented);
    rentedMsg({
      ...rented,
      showEditForm: true,
      start: new Date(rented.start),
      end: new Date(rented.end),
    });
  };
  return (
    <div
      onDoubleClick={DclickHandle}
      ref={drag}
      style={{
        display: "flex",
        boxSizing: "border-box",
        // height: `${cellH}px`
        // backgroundColor: 'black'
      }}
    >
      <div
        style={{
          borderTop: `${cellH / 2.5}px solid transparent`,
          borderRight: `${w / 4.5}px solid #5abe9d`,
          borderBottom: `${cellH / 2.5}px solid transparent`,
        }}
      />

      <div
        ref={rentedRef}
        // key={id}
        onMouseOver={() => idMsg(rented)}
        onMouseOut={() => idMsg(false)}
        className={rented.className}
        style={{
          display: "flex",
          justifyContent: "flex-start",
          cursor: "pointer",
          width: `${(rented.width / 100) * (w * 2) - w / 2}px`,
        }}
      >
        <div
          className="name-container-container"
          style={{
            width: `${width - 40}px`,
            textAlign: "center",
            overflowX: "hidden",
          }}
        >
          <div
            // className="name-container"
            style={{
              paddingLeft: `${left <= 0 ? -left : 0}px`,
              boxSizing: "border-box",
              color: "black",
              fontSize: "11px",
            }}
          >
            {rented.name}
          </div>
        </div>
      </div>
      <div
        style={{
          //
          borderTop: `${cellH / 2.5}px solid transparent`,
          borderLeft: `${w / 4.5}px solid #5abe9d`,
          borderBottom: `${cellH / 2.5}px solid transparent`,
        }}
      />
    </div>
  );
};

