import React from "react";
import TextField from "@mui/material/TextField";
import MenuItem from "@mui/material/MenuItem";

import { useController } from "react-hook-form";

import "./InputForm.css"; // Assuming you have a CSS file for styles

export const InputForm = ({
  control,
  name,
  value,
  type,
  select,
  options,
  rules,
  handleChange,
  className,
  multiline, // Added multiline prop
  rows,
  style,
  label,
}) => {
  const { field, fieldState } = useController({
    name,
    control,
    rules: { required: true, ...rules },
  });

  return (
    <div className="">
      {select ? (
        <TextField
          select
          value={value}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          label={label}
          helperText={fieldState.error ? fieldState.error.message : null}
        >
          {options.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </TextField>
      ) : (
        <TextField
          inputProps={{ type, className }}
          onChange={(e) => {
            field.onChange(e.target.value);
            handleChange(e.target.value);
          }}
          onBlur={field.onBlur}
          style={style}
          value={value}
          name={field.name}
          multiline={multiline}
          rows={rows}
          className={className}
          type={type}
          inputRef={field.ref}
          label={label}
          rules={rules}
          // inputFormat="dd-MM-yyyy"
        />
      )}
      <div className="errorContainer">
        {fieldState.error && (
          <p className="errorMessage">{fieldState.error.message}</p>
        )}
      </div>
    </div>
  );
};
