//   DndProvider,
//   TouchTransition,
//   MouseTransition,
// } from "react-dnd-multi-backend";
import {
  BrowserRouter,
  Switch,
  Routes,
  useNavigate,
  Router,
  Route,
  Link,
  Navigate,
  useRouteMatch,
} from "react-router-dom";
import { MultiBackend } from "react-dnd-multi-backend";
import {
  DndProvider,
  TouchTransition,
  MouseTransition,
} from "react-dnd-multi-backend";
// import { HTML5toTouch } from "rdndmb-html5-to-touch";
import LoginForm from "./components/Form/LoginForm";

export default function App() {
  const HTML5toTouch = {
    backends: [
      {
        id: "html5",
        backend: HTML5Backend,
        transition: MouseTransition,
      },
      {
        id: "touch",
        backend: TouchBackend,
        options: { enableMouseEvents: true },
        preview: true,
        transition: TouchTransition,
      },
    ],
  };
  // sk-0fdc92f1d7ad4ba3957a46fb097e003f
  const [isLoggedIn, setIsLoggedIn] = useState(false); // Agrega estado para controlar si el usuario está autenticado

  return (
    <IdProvider>
      <FormProvider>
        <DatesProvider>
          <SocketProvider>
            <ResDataProvider>
              <RentedProvider>
                <Provider>
                  <DndProvider options={HTML5toTouch}>
                    {/* Si el usuario está autenticado, redirige a la página ContainerContainer */}
                    {isLoggedIn ? (
                      <Navigate to="/app" />
                    ) : (
                      <BrowserRouter>
                        <Routes>
                          <Route path="/" exact element={<LoginForm />}>
                            {/* Aquí irá tu formulario de inicio de sesión */}

                            {/* Puedes manejar el inicio de sesión y establecer setIsLoggedIn(true) */}
                          </Route>
                          {/* Ruta protegida que requiere inicio de sesión */}
                          <Route
                            path="/app"
                            exact
                            element={<ContainerContainer />}
                          ></Route>
                        </Routes>
                      </BrowserRouter>
                    )}
                  </DndProvider>
                </Provider>
              </RentedProvider>
            </ResDataProvider>
          </SocketProvider>
        </DatesProvider>
      </FormProvider>
    </IdProvider>
  );
}
