import createDataContext from "./createDataContext";
import { io } from "socket.io-client";
const MSGS = {
  SOCKET_CONNECTION: "SOCKET_CONNECTION",
};
const socketMsg = (dispatch) => (socket) => {
  dispatch({ type: MSGS.SOCKET_CONNECTION, socket });
};

const SocketContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.SOCKET_CONNECTION: {
      // const socket = io("https://localhost:8000");
      const socket = io();

      return { ...state, socket };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  SocketContext,
  {
    socketMsg,
  },
  {
    socket: null,
  }
);
