import createDataContext from "./createDataContext";
import { io } from "socket.io-client";
const MSGS = {
  RES_DATA: "RES_DATA",
};
const resMsg = (dispatch) => (resData) => {
  dispatch({ type: MSGS.RES_DATA, resData });
};

const ResDataContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.RES_DATA: {
      const { resData } = msg;

      return { ...state, resData };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  ResDataContext,
  {
    resMsg,
  },
  {
    data: null,
  }
);
