import createDataContext from "./createDataContext";
import { addYears, formatWithOptions, format } from "date-fns/fp";
import { es } from "date-fns/locale";
import { u } from "react-uuid";

import {
  eachDayOfInterval,
  add,
  addDays,
  subDays,
  differenceInDays,
  getDaysInMonth,
  isFirstDayOfMonth,
  isLastDayOfMonth,
} from "date-fns";
import _ from "lodash";

const MSGS = {
  ADD_DAY: "ADD_DAY",
  SUB_DAY: "SUB_DAY",
  NEW_DAY: "NEW_DAY",
  NEW_INTERVAL_RENTED: "NEW_INTERVAL_RENTED",
  INTERVAL_RENTED_DB: "INTERVAL_RENTED_DB",
  START_DATE: "START_DATE",
  SEARCH_NAME: "SEARCH_NAME",
  REMOVE_BY_DRAGGING: "REMOVE_BY_DRAGGING",
  SEND_INTERVAL_FOR_SOCKET: "SEND_INTERVAL_FOR_SOCKET",
  EDITING: "EDITING",
  DELETE_INTERVAL_RENTED: "DELETE_INTERVAL_RENTED",
};

const deleteMsg = (dispatch) => (intervalRentedId) => {
  dispatch({
    type: MSGS.DELETE_INTERVAL_RENTED,
    intervalRentedId,
  });
};

const editingMsg = (dispatch) => (intervalRented) => {
  console.log(intervalRented);
  dispatch({
    type: MSGS.EDITING,
    intervalRented,
  });
};

const removeByDragMsg =
  (dispatch) =>
  ({ price, nights, width, email, phone, name, id, start, room, end }) => {
    dispatch({
      type: MSGS.REMOVE_BY_DRAGGING,
      id,
      start,
      room: room - 1,
      email,
      phone,
      name,
      end,
      width,
      nights,
      price,
    });
  };
const sendIntervalForSocketMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.SEND_INTERVAL_FOR_SOCKET,
    intervalRented,
  });
};

const searchNameMsg = (dispatch) => (searchName) => {
  dispatch({
    type: MSGS.SEARCH_NAME,
    searchName,
  });
};
const loadIntervals_db_msg = (dispatch) => (intervalsArray_db) => {
  dispatch({
    type: MSGS.INTERVAL_RENTED_DB,
    intervalsArray_db,
  });
};
const newDayMsg = (dispatch) => (day) => {
  dispatch({
    type: MSGS.NEW_DAY,
    day,
  });
};

const addIntervalMsg = (dispatch) => (intervalSign, startDate) => {
  dispatch({
    type: MSGS.ADD_DAY,
    intervalSign,
    startDate,
  });
};
const addIntervalToArrayRentedMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.NEW_INTERVAL_RENTED,
    intervalRented,
  });
};
const modifyIntervalToArrayRentedMsg = (dispatch) => (intervalRented) => {
  dispatch({
    type: MSGS.EDITING,
    intervalRented,
  });
};
const DatesContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.REMOVE_BY_DRAGGING: {
      const { nights, width, email, phone, name, id, start, room, end, price } =
        msg;

      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRented = {
        end: formatWithOptions({ locale: es }, "yyyy/MM/dd")(end),
        start: formatWithOptions({ locale: es }, "yyyy/MM/dd")(start),
        room,
        name,
        id,
        email,
        phone,
        nights,
        price,
        width,
        className: "draggable",
      };

      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return interval.id === newIntervalRented.id
          ? newIntervalRented
          : interval;
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,

          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }

    case MSGS.SEARCH_NAME: {
      const { searchName } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;
      //   const cleanIdArray = searchedIds.filter((v, i, a) => a.indexOf(v) === i);
      const searchedName = intervalRentedArray.filter((interval) => {
        return interval.name === searchName;
      });
      const ids = searchedName
        .map((interval) => interval.id)
        .filter((v, i, a) => a.indexOf(v) === i);

      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return ids.find((id) => id === interval.id)
          ? { ...interval, className: "searched" }
          : { ...interval, className: "draggable" };
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRentedArray: newIntervalRentedArray,
        },
        searchName,
        searchedIds: ids,
      };
    }

    case MSGS.START_DATE: {
      const { startDate } = msg;
      return { ...state, startDate };
    }
    case MSGS.SEND_INTERVAL_FOR_SOCKET: {
      const { intervalRented } = msg;

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented,
        },
      };
    }

    case MSGS.INTERVAL_RENTED_DB: {
      const { intervalsArray_db } = msg;

      const {
        calendar: { intervalRentedArray },
      } = state;

      const newIntervalRentedArray_db = intervalsArray_db.map((interval) => {
        // const startDate = addDays(new Date(interval.start), 1);
        // const endDate = addDays(new Date(interval.end), 1);
        const formattedDateStart = new Date(interval.start)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");
        const formattedDateEnd = new Date(interval.end)
          .toISOString()
          .slice(0, 10)
          .replace(/-/g, "/");

        return {
          // start: formatWithOptions({ locale: es }, "yyyy/MM/dd")(interval.end),
          // end: formatWithOptions({ locale: es }, "yyyy/MM/dd")(interval.start),
          start: formattedDateEnd,
          end: formattedDateStart,
          room: interval.room,
          id: interval._id,
          name: interval.name,
          email: interval.email,
          phone: interval.phone,
          nights: interval.nights,
          price: interval.price,
          width:
            differenceInDays(new Date(interval.end), new Date(interval.start)) *
            100,
          className: "draggable",
          adelanto: interval.adelanto,
          comments: interval.comments,
          dni: interval.dni,
          montoPendiente: interval.montoPendiente,
          nombre_recepcionista: interval.nombre_recepcionista,
          precioTotal: interval.precioTotal,
          time: interval.time,
        };
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,

          intervalRentedArray: newIntervalRentedArray_db,
        },
      };
    }

    case MSGS.NEW_INTERVAL_RENTED: {
      const { intervalRented } = msg;
      console.log(intervalRented);
      const {
        calendar: { intervalRentedArray },
      } = state;

      const formattedDateStart = new Date(intervalRented.start)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");
      const formattedDateEnd = new Date(intervalRented.end)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");

      const {
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        room,
        id,
        time,
        nights,
        nombre_recepcionista,
        phone,
        precioTotal,
        price,
      } = intervalRented;

      const newIntervalRented = {
        start: formattedDateEnd,
        end: formattedDateStart,
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        nights,
        id,
        nombre_recepcionista,
        phone,
        time,
        precioTotal,
        room,
        width: differenceInDays(intervalRented.end, intervalRented.start) * 100,
        className: "draggable",
        price,
      };
      console.log(newIntervalRented);
      const newIntervalRentedArray = [
        ...intervalRentedArray,
        newIntervalRented,
      ];

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented: newIntervalRented,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }

    case MSGS.EDITING: {
      const { intervalRented } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;
      const formattedDateStart = new Date(intervalRented.start)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");
      const formattedDateEnd = new Date(intervalRented.end)
        .toISOString()
        .slice(0, 10)
        .replace(/-/g, "/");

      const {
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        room,
        id,
        time,
        nights,
        nombre_recepcionista,
        phone,
        precioTotal,
        price,
      } = intervalRented;
      const newIntervalRented = {
        start: formattedDateEnd,
        end: formattedDateStart,
        adelanto,
        comments,
        dni,
        email,
        montoPendiente,
        name,
        nights,
        id,
        nombre_recepcionista,
        phone,
        time,
        precioTotal,
        room,
        width: differenceInDays(intervalRented.end, intervalRented.start) * 100,
        className: "draggable",
        price,
      };
      const newIntervalRentedArray = intervalRentedArray.map((interval) => {
        return interval.id === newIntervalRented.id
          ? newIntervalRented
          : interval;
      });

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRented: newIntervalRented,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }
    case MSGS.DELETE_INTERVAL_RENTED: {
      const { intervalRentedId } = msg;
      const {
        calendar: { intervalRentedArray },
      } = state;

      console.log("Before filter:", intervalRentedArray, intervalRentedId);

      const newIntervalRentedArray = intervalRentedArray.filter(
        (interval) => interval.id !== intervalRentedId.id
      );

      console.log("After filter:", newIntervalRentedArray);

      return {
        ...state,
        calendar: {
          ...state.calendar,
          intervalRentedArray: newIntervalRentedArray,
        },
      };
    }

    case MSGS.ADD_DAY: {
      const { newIntervalSign } = msg;
      const {
        calendar: {
          intervalData,
          generalDatesInterval,
          intervalRentedArray,
          intervalSignFront,
          counter,
        },
        hotelRooms,
      } = state;

      const { intervalSign, startDate } = msg;

      const start =
        intervalSign === "+"
          ? addDays(generalDatesInterval.start, 1)
          : intervalSign === "startDate"
          ? startDate
          : subDays(generalDatesInterval.start, 1);

      const end =
        intervalSign === "+"
          ? addDays(generalDatesInterval.end, 1)
          : subDays(generalDatesInterval.end, 1);
      const newGeneralDatesInterval = { start, end };

      const newIntervalData = eachDayOfInterval(newGeneralDatesInterval).map(
        (day) => {
          return {
            formDate: day,
            day: formatWithOptions({ locale: es }, "d")(day),
            dayName: formatWithOptions({ locale: es }, "EE")(day),
            date: formatWithOptions({ locale: es }, "yyyy/MM/dd")(day),
            month: formatWithOptions({ locale: es }, "MMMM")(day),
          };
        }
      );
      const newIntervalMonth = eachDayOfInterval(newGeneralDatesInterval).map(
        (day) => ({
          firstDay: isFirstDayOfMonth(day) ? day : null,
          lastDay: isLastDayOfMonth(day) ? day : null,
          monthDate: day,
          date: day,
          month: formatWithOptions({ locale: es }, "MMMM")(day),
          year: formatWithOptions({ locale: es }, "d")(day),
          monthDay: formatWithOptions({ locale: es }, "d")(day),
          width: getDaysInMonth(day),
        })
      );
      const newCounter = intervalSign === "+" ? counter + 1 : counter - 1;
      return {
        ...state,
        calendar: {
          counter: newCounter,
          intervalSignFront: intervalSign,
          intervalMonth: newIntervalMonth,
          intervalData: newIntervalData,
          generalDatesInterval: newGeneralDatesInterval,
          intervalRentedArray,
          startDate,
        },
      };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  DatesContext,
  {
    newDayMsg,
    searchNameMsg,
    addIntervalMsg,
    addIntervalToArrayRentedMsg,
    loadIntervals_db_msg,
    removeByDragMsg,
    sendIntervalForSocketMsg,
    editingMsg,
    modifyIntervalToArrayRentedMsg,
    deleteMsg,
  },
  {
    hotelRooms: _.range(1, 16),
    searchName: "",
    searchedIds: [],
    calendar: {
      generalDatesInterval: {
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      },
      intervalRented: {},
      intervalRentedArray: [],
      startDate: new Date(),
      intervalSignFront: "+",
      intervalData: eachDayOfInterval({
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      }).map((day) => ({
        formDate: day,
        day: formatWithOptions({ locale: es }, "d")(day),
        dayName: formatWithOptions({ locale: es }, "EE")(day),
        date: formatWithOptions({ locale: es }, "yyyy/MM/dd")(day),
        month: formatWithOptions({ locale: es }, "MMMM")(day),
      })),
      counter: 0,

      intervalMonth: eachDayOfInterval({
        start: new Date(),
        end: add(new Date(), { months: 4 }),
      }).map((day) => ({
        firstDay: isFirstDayOfMonth(day) ? day : null,
        lastDay: isLastDayOfMonth(day) ? day : null,
        monthDate: day,
        month: formatWithOptions({ locale: es }, "MMMM")(day),
        year: formatWithOptions({ locale: es }, "d")(day),
        monthDay: formatWithOptions({ locale: es }, "d")(day),
        date: day,
        width: getDaysInMonth(day),
      })),
    },
  }
);
