import React, { useState, useContext, useEffect } from "react";
import { Context as FormContext } from "../../context/FormContext";
import { Navigate } from "react-router-dom";
import RegisterForm from "./RegistrationForm";
import { Context as SocketContext } from "../../context/SocketContext";
function LoginForm() {
  // const navigate = useNavigate();
  const { userIdMsg } = useContext(FormContext); // Obtener el método para enviar el mensaje del contexto
  const [loggedIn, setLoggedIn] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const { socketMsg } = useContext(SocketContext);
  const { socket } = useContext(SocketContext);
  const [data, setData] = useState(null);

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      socketMsg();
      const response = await fetch("/auth/login", {
        // const response = await fetch("http://localhost:8000/auth/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, password }),
      });
      const data = await response.json();
      if (response.ok) {
        setLoggedIn(true);
        // Aquí envías el mensaje al FormContext con el userId

        userIdMsg(data.userId);

        Navigate("/app"); // Redirigir al usuario a la página de inicio después del inicio de sesión
        setData(data);
      } else {
        setError(data.message);
      }
    } catch (error) {
      setError("Error al iniciar sesión");
    }
  };

  if (loggedIn) {
    // Redirigir al usuario a la página de inicio después del inicio de sesión

    return <Navigate to="/app" />;
  }

  return (
    <div>
      <h2>Iniciar Sesión</h2>
      {error && <p style={{ color: "red" }}>{error}</p>}
      <form onSubmit={handleLogin}>
        <div>
          <label>Usuario:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div>
          <label>Contraseña:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <button type="submit">Iniciar Sesión</button>
      </form>
      <RegisterForm />
    </div>
  );
}

export default LoginForm;
