import React from 'react';
import { Context } from '../../context/DatesContext';
import { useContext, useEffect, useState } from 'react';
import { height } from '@mui/system';

export const MonthRef2 = ({ label, w, interval }) => {
	const { state } = useContext(Context);
	const { calendar } = state;
	const { intervalSignFront } = calendar;

	const { intervalMonth } = calendar;

	const start = intervalMonth[0].monthDay;

	const [ end, setEnd ] = useState(
		intervalMonth.slice(0, interval - 1)[intervalMonth.slice(0, interval - 1).length - 1].monthDay
	);

	useEffect(
		() => {
			setEnd(intervalMonth.slice(0, interval - 1)[intervalMonth.slice(0, interval - 1).length - 1].monthDay);
		},
		[ interval ]
	);

	// const middle = intervalMonth.slice(0, slice)[4].monthDay;
	const superWidth = end * w;

	const paddingLeft = (start - 1) * w;
	const [ counterBack, setCounterBack ] = useState(30);

	// useEffect(() => {
	//   if (start >= 16 && intervalMonth[0].month === label) {
	//     setCounterBack(counterBack - 1);
	//   }
	// }, [intervalMonth]);

	return (
		<div
			style={{
				boxSizing: 'border-box',
				display: 'flex',
				justifyContent: 'center',
				textAlign: 'center',
				flexDirection: 'column',
				width: `${start >= 30 - interval && intervalMonth[0].month === label ? counterBack * w : superWidth}px`,
				color: 'white',
				fontSize: '11px',
				height: '3vh'
				// backgroundColor: 'lightgreen'
			}}
		>
			<div
				style={
					start >= 2 && start <= 30 - interval ? (
						{
							// backgroundColor: "blue",
							paddingLeft: `${paddingLeft}px`,
							boxSizing: 'border-box',
							display: 'flex',
							flexDirection: 'column',
							justifyContent: 'center'
						}
					) : start > 30 - interval && intervalMonth[0].month === label ? (
						{
							// backgroundColor: "red",
							paddingLeft: `${paddingLeft}px`,
							boxSizing: 'border-box',
							display: 'flex',
							flexDirection: 'column',
							justifyContent: 'center'
						}
					) : (
						{}
					)
				}
			>
				{label}
			</div>
		</div>
	);
};
