import createDataContext from "./createDataContext";

const MSGS = {
  RENTED: "RENTED",
};
const rentedMsg = (dispatch) => (rented) => {
  dispatch({
    type: MSGS.RENTED,
    rented,
  });
};

const RentedFormContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.RENTED: {
      const { rented } = msg;

      return { ...state, rented };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  RentedFormContext,
  {
    rentedMsg,
  },
  {
    rented: { showEditForm: false },
  }
);
