///////////FORM MESSAGES///////////////////////////

const FormContext = (state, msg) => {
  switch (msg.type) {
    case MSGS.USER_ID: {
      const { userId } = msg;
      return {
        ...state,
        userId,
      };
    }
    case MSGS.EDIT_FORM: {
      console.log("edittttttttttttttttttttttformmmmmmmmmmmmmmmmm");
      const {
        showEditForm,
        start,
        end,
        nights,
        room,
        price,
        name,
        phone,
        email,
        id,
        width,
      } = msg;
      console.log(id);
      const { intervalRented } = state;

      return {
        ...state,
        showEditForm,
        intervalRented: {
          ...intervalRented,
          start,
          nights,
          room,
          end,
          price,
          name,
          phone,
          email,
          id,
          width,
        },
      };
    }

    case MSGS.FORM_SUBMITTED_CONTAINER: {
      const { formSubmittedContainer } = msg;

      return { ...state, formSubmittedContainer };
    }
    case MSGS.FORM_SUBMITTED: {
      const { formSubmitted } = msg;

      return { ...state, formSubmitted };
    }
    case MSGS.SOCKET_DATA: {
      const { socketData } = msg;

      return { ...state, socketData };
    }
    case MSGS.SHOW_FORM: {
      const { rented } = msg;

      const endd =
        rented.formType === "edit"
          ? rented.end
          : add(rented.start, { days: 1 });

      return {
        ...state,

        intervalRented: {
          ...rented,
          end: endd,
        },
      };
    }

    case MSGS.START: {
      const { start } = msg;
      console.log(start);
      const { intervalRented } = state;
      const { end } = intervalRented;
      const newNightNumber = differenceInDays(end, start);
      return {
        ...state,
        intervalRented: {
          ...intervalRented,
          start,
          nights: newNightNumber.toString(),
        },
      };
    }
    case MSGS.END: {
      const { end } = msg;
      console.log(end);
      const { intervalRented } = state;
      const { start } = intervalRented;
      const newNightNumber = differenceInDays(end, start);
      return {
        ...state,
        intervalRented: {
          ...intervalRented,
          end,
          nights: newNightNumber.toString(),
        },
      };
    }
    case MSGS.PHONE: {
      const validatePhone = (phone) => {
        // regex to check format
        const regex = /^\d{4}-\d{6}$/;

        if (!phone) return "Phone number is required";

        if (!regex.test(phone)) {
          return "Phone number must be 10 digits";
        }
      };
      const { phone } = msg;
      const { intervalRented } = state;
      const phoneError = validatePhone(phone);
      console.log(phoneError);

      if (phoneError) {
        return state;
      }

      // update state
      return {
        ...state,
        intervalRented: {
          ...intervalRented,
          phone,
        },
      };
    }
    case MSGS.EMAIL: {
      const { email } = msg;
      const { intervalRented } = state;
      return {
        ...state,
        intervalRented: { ...intervalRented, email },
      };
    }
    case MSGS.NAME: {
      const { name } = msg;
      const nameError = name.trim() === "" ? "Name is required" : null;
      const { intervalRented } = state;
      return {
        ...state,
        intervalRented: { ...intervalRented, name, nameError },
      };
    }
    case MSGS.ROOM: {
      const { room } = msg;
      const { intervalRented } = state;
      return {
        ...state,
        intervalRented: { ...intervalRented, room },
      };
    }
    case MSGS.PRICE: {
      const { price } = msg;
      const { intervalRented } = state;
      const { nights, priceNights } = intervalRented;
      const nNights = parseInt(nights);
      const nPrice = parseFloat(price);
      const totalPrice = nPrice * nNights;

      return {
        ...state,
        intervalRented: { ...intervalRented, price, priceNights: totalPrice },
      };
    }
    case MSGS.NIGHTS_NUMBER: {
      const { nights } = msg;
      console.log(nights);
      const { intervalRented } = state;
      const { start, price } = intervalRented;

      const nNights = parseInt(nights);
      const nPrice = parseFloat(price);
      const totalPrice = nPrice * nNights;

      const endFromNights = add(start, { days: nights });

      const defaultDate = new Date(endFromNights);
      const formattedDate_end = defaultDate.toISOString().split("T")[0];
      return {
        ...state,
        intervalRented: {
          ...intervalRented,
          nights,
          end: formattedDate_end,
          priceNights: totalPrice,
        },
      };
    }

    case MSGS.INTERVAL_RENTED: {
      const { intervalRented } = msg;
      return { ...state, intervalRented };
    }

    default:
      return state;
  }
};
export const { Context, Provider } = createDataContext(
  FormContext,
  {
    intervalRentedMsg,
    startMsg,
    endMsg,
    phoneMsg,
    nameMsg,
    emailMsg,
    roomMsg,
    priceMsg,
    showFormMsg,
    nightsNumberMsg,
    socketDataMsg,
    formSubmittedMsg,
    formSubmittedContainerMsg,
    editFormMsg,
    userIdMsg,
  },
  {
    formSubmittedContainer: false,
    formSubmitted: false,
    socketData: {},

    showEditForm: false,
    intervalRented: {
      room: 1,
      name: "",
      showForm: false,
      start: new Date(),
      end: add(new Date(), { days: 1 }),
      width: 0,
      email: "",
      phone: "",
      nights: "1",
      price: "",
      priceNights: "",
      id: "",
      userId: null,
    },
  }
);
